import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Display3.css';

function Display3() {
  const [currentSlide, setCurrentSlide] = useState(0);
 

  const slides = [
    [
      "https://ps-family.com.ua/images/gamanec-my-7320x240.jpg?crc=330629840",
      "https://images.prom.ua/4130880377_w640_h640_4130880377.jpg",
      "https://cdn.goodhouse.com.ua/images-jpg/16324/163241.jpg",
      "https://barnews.city/upload/article/o_1dl577hsc1pqdie0146i1svk6gg2a.jpg",
      "https://img.joomcdn.net/892af501f5bd078e048ade81a8ec0a5ebe1e0eac_original.jpeg",
      "https://i.ytimg.com/vi/SeXF-WGw384/maxresdefault.jpg",
      "https://image-thumbs.shafastatic.net/753106043_310_430",
      "https://prostobzor.com/wp-content/uploads/2022/06/garmin-fenix-watches.jpg"
    ],
    [
      "https://images.prom.ua/3524764400_w640_h640_3524764400.jpg",
      "https://images.shafastatic.net/525010676",
      "https://rozhyshche.rayon.in.ua/upload/news/2/1562924697211/1_gamanets-1.jpg",
      "https://images.shafastatic.net/210889677",
      "https://images.shafastatic.net/127308175",
      "https://futurenow.com.ua/wp-content/uploads/2021/09/facebook-i-ray-ban-predstavyly-kruti-rozumni-okulyary-ray-ban-stories-scaled.jpg",
      "https://ireland.apollo.olxcdn.com/v1/files/acpmgzeindqx2-UA/image;s=644x461",
      "https://image-thumbs.shafastatic.net/816925521_310_430"
    ],
    
    // додайте більше масивів для додаткових слайдів
  ];

  const handleClick = (index) => {
    setCurrentSlide(index);
  }

  return (
    <div className="container-3 container-def">
      <div className="filters">{/* фільтри */}</div>

      {slides.map((slide, index) => (
        <div 
          className={`grid-container ${index === currentSlide ? 'active' : ''}`} 
          key={index}
        >
          <div className="grid-row">
            {slide.slice(0, 4).map((image, index) => (
              <div className="grid-col" key={index}>
                <Link to="/lost-and-found-test/item">
                  <img src={image} alt={`Image ${index + 1}`} />
                </Link>
              </div>
            ))}
          </div>

          <div className="grid-row">
            {slide.slice(4).map((image, index) => (
              <div className="grid-col" key={index}>
                <Link to="/lost-and-found-test/item">
                  <img src={image} alt={`Image ${index + 5}`} />
                </Link>
              </div>
            ))}
          </div>
        </div>
      ))}

      <div className="page-pagination">
        {slides.map((_, index) => (
          <button 
            className="pagination-button"
            onClick={() => handleClick(index)}
            key={index}
          >
            {index + 1}
          </button>
        ))}
      </div>
    </div>
  );
}

export default Display3;
