import React, { useState, useRef } from 'react';
import profileIcon from '../../img/profile-icon.png';
import CheckIcon from '../../img/check-mark.svg';
import './Advertise.css';
import Iframe from 'react-iframe';
import Header from '../Header/Header';

function Advertise() {
  
    const [phoneIsClicked, setPhoneIsClicked] = useState(false);
    const [emailIsClicked, setEmailIsClicked] = useState(false);
  
    const handlePhoneClick = () => {
      setPhoneIsClicked(true);
    }

    const handleEmailClick = () => {
      setEmailIsClicked(true);
    }
  
  return (
    <div className="advertise container-def">
      <Header></Header>
      <div>
          <div className="form-row">
            <div className="form-column">
            <div
                className="image-field"
                style={{
                  width: '250px',
                  height: '250px',
                  backgroundImage: `url(https://images.prom.ua/4130880377_w640_h640_4130880377.jpg)`,
                  backgroundSize: 'cover',
                  borderRadius: '35px',
                }}
              >
              
              </div>
            </div>
            <div className="form-column">
              <div className='item-name'>Гаманець чоловічий</div>
              <div className='item-location'>Львів, вул. С.Бандери 1</div>
              <div className='item-reward'>Присутня винагорода
              <img src={CheckIcon}></img>
              </div>
            </div>
            <div className="form-column">
              <div className='profile-block'>
                <div className='photo-and-name'>
                  <img src={profileIcon}></img>
                  <span className='user-name'>Віталій</span>
                </div>
                <p>Звязатися з людиною за допомогою:</p>
                <button onClick={handlePhoneClick} className='phone-button'>
                  {phoneIsClicked ? '098 456 12 45' : 'Номер телефону'}
                </button>
                <button onClick={handleEmailClick}>
                  {emailIsClicked ? 'anidrew23@gmail.com' : 'Написати повідомлення'}
                </button>
              </div>
            </div>
          </div>
          <div className="form-row">
          <div className="form-column">
            <div className='item-description'>
              <p>Знайшов загублений синій чоловічий гаманець у парку на лавці. Містить документи, гроші та банківські картки. Гаманець у хорошому стані. З винагородою за повернення. Зверніться, якщо впізнали. Дякую.</p>
              <span className='views-count'>Переглядів 1237</span>
            </div>
          </div>
          <div className="form-column">
            <div className='map-location'>
              <p>Місцезнаходження на Карті:</p>
              <Iframe
              url="https://snazzymaps.com/embed/496215"
              width="100%"
              height="100%"
              allowFullScreen
              
              referrerPolicy="no-referrer-when-downgrade"
            />
            </div>
          </div>
          </div>
          <div className="form-row">
          </div>
      </div>
    </div>
  );
}

export default Advertise;
