import './MainDisplay.css';
import React, { useState, useRef } from 'react';
import SwiperCore, { Navigation, Pagination, Mousewheel } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.css';
import Display1 from '../Display1/Display1';
import Display2 from '../Display2/Display2';
import Display3 from '../Display3/Display3';
import Display4 from '../Display4/Display4';
import Advertise from '../Advertise/Advertise';

SwiperCore.use([Navigation, Pagination, Mousewheel]);

function MainDisplay() {
  const [activeSlideIndex, setActiveSlideIndex] = useState(0);
  const touchStartX = useRef(0);

  const handleSlideChange = (swiper) => {
    setActiveSlideIndex(swiper.activeIndex);
  };

  const handleTouchStart = (event) => {
    if (event.touches && event.touches[0]) {
      touchStartX.current = event.touches[0].clientX;
    }
  };

  const handleTouchMove = (event) => {
    if (event.touches && event.touches[0]) {
      const touchEndX = event.touches[0].clientX;
      const touchDistance = touchEndX - touchStartX.current;
  
      if (touchDistance > 50) {
        swiperRef.current.slidePrev();
      } else if (touchDistance < -50) {
        swiperRef.current.slideNext();
      }
    }
  };

  const swiperRef = useRef(null);

  return (
    <div className="main-display">
      <Swiper
        direction="vertical"
        slidesPerView={1}
        navigation={false}
        pagination={{ clickable: true }}
        onSlideChange={handleSlideChange}
        mousewheel={{
          forceToAxis: true,
        }}
        touchMove={true}
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onSwiper={(swiper) => (swiperRef.current = swiper)}
      >
        <SwiperSlide>
          <Display1 />
        </SwiperSlide>
        <SwiperSlide>
          <Display2 />
        </SwiperSlide>
        <SwiperSlide>
          <Display3 />
        </SwiperSlide>
        <SwiperSlide>
          <Display4 />
        </SwiperSlide>
        
      </Swiper>

      
    </div>
  );
}

export default MainDisplay;
