import React, { useState } from 'react';
import { FaBars, FaSearch, FaUser } from 'react-icons/fa';
import logo from '../../img/logo.svg';
import accountImg from '../../account.png';
import signIn from '../../img/sigin.svg';
import './Header.css';

import { Link } from 'react-router-dom';

function Header() {
  const [isUserMenuOpen, setUserMenuOpen] = useState(false);

  return (
    <header className="header">
      <label className='burger-menu'>
        <input type="checkbox"></input>
        <span className="menu"> <span className="hamburger"></span> </span>
        <ul>
          <li> <a href="#">mail@gmail.com</a> </li>
          <li> <a href="#">+38 098 34 58 736</a> </li>
          <li> <a href="#">Social</a> </li>
          <li> <Link to="/lost-and-found-test/about-us">Про нас</Link> </li>
        </ul>
      </label>
      <div className="logo">
        <Link to="/lost-and-found-test">
          <span className='logo-span'>Find My Thing</span>
        </Link>
      </div>
      <div className="buttons-header">
        <Link to="/lost-and-found-test/find">
          <button className="find-btn">Знайшов</button>
        </Link>
        <Link to="/lost-and-found-test/lost">
          <button className="search-btn">Шукаю</button>
        </Link>
        <div
        className="account-icon"
      >
        <Link to="/lost-and-found-test/login">
        <img src={accountImg} alt="description of the image" />
        </Link>
      </div>
      </div>
    </header>
  );
}

export default Header;
